    margin: .5rem;
    color: black;
    text-decoration: none;
}

.Pokecard-id
{
    padding-top: .5rem;
    font-weight: 300;
}

.Pokecard-name
{
    font-weight: 300;
    padding-bottom: .25rem;
}

.Pokecard-img
{
    width: 85%;
    margin: auto;
}

.Pokecard-types
{
    display: flex;
    justify-content: center;
    align-items: center;
}

.Pokecard-types img
{
